import React, { Component } from 'react'
import './css.css';
import utils from '../../library'
import MetaLink from '../MetaLink/MetaLink'
import CollectionLink from '../MetaLink/CollectionLink'

class Cards extends Component {
    MetaList = () => {
      let collections

      if(this.props.net==80001)
      collections=[
        "0xbc49e488f155c45c0Ba09128B41C8C3d839161A7",
        "0x02410B060A1Af1651bbE30c8cc59C38f4cce4CBd",
       ]

       if(this.props.net==137)
       collections=[
         "",
        ]

      if(this.props.net==3)
       collections=[
         "0x89b6AC1B89Fade31100588f50903a3659F307a61",
         "0xad2644F1DC876AF4259d7c7d503b764B26A91c3b"
        ]
        let listItems = collections.map((addr) =>
          // Correct! Key should be specified inside the array.
          <CollectionLink address={addr} key={Math.random()}/>
      
        );
        return (
          <>
            {listItems}
          </>
        );
      }

    render(){
        utils.setHomeColors()
        console.log(this.props.update) 
        return ( 
          <>
       

        <div id="cards_flyer">
          <div id="cards_mainlabel">Cypher's Cards Collections</div>
          <img id="cards_imageMain"  alt="NFT"  src="https://cdn.discordapp.com/attachments/751252665085984882/825465842678235176/cybs_back.png"  onClick={() => this.props.update("Features")}></img>
         
            <br></br> 
            
        </div>
     
        <div className="Collections_container">
        <div id="cards_sublabel">Curated Collections</div>
        {this.MetaList()}

        </div>
        </>
        );
    }


}




export default Cards